header{
	background:#3c3c3c;
	width:100%; 
    height: 80px;
	min-width: $min-width;
	text-align:left;	
	order: 1;	
    -webkit-order: 1;	
    z-index: 4;
}
.main-logo{
    float: left;
    margin-top: 8px;
    margin-left: 18px;
}
.button-nav {
  width: 22px;
  height: 18px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  float: right;
  width: 22px;
  margin: 31px 11px 0 0px;
  padding: 0px;
  z-index: 10000; 
  .button-nav__cont {
    position: relative;
    display: block; }
  .line {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background: #ffffff;
    border-radius: 0px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .35s ease-in-out;
    -moz-transition: .35s ease-in-out;
    -o-transition: .35s ease-in-out;
    transition: .35s ease-in-out;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px; 
    @include shadow(0px 2px 0px 0px rgba(0,0,0,0.5));}
  .line:nth-child(1) {
    top: 0px; }
  .line:nth-child(2) {
    top: 7px; }
  .line:nth-child(3) {
    top: 14px; }
  &.active .line:nth-child(1) {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 10px; }
  &.active .line:nth-child(2) {
    opacity: 0; }
  &.active .line:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    top: 10px; }
}

.head-menu{
    display: none;
}



