//main styles
.main-wrapper {
    padding: 0 0 0 0;
    min-width: $min-width;
    width: 100%;
    position: relative;
    overflow: hidden;
    min-height: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    justify-content: flex-start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
}

.wrapper {
    min-width: $min-width;
    max-width: $max-width;
    padding: 0 20px 0 20px;
    margin: 0 auto;
    position: relative;
}

/* titles */

p {
    @include font($base-font-size, $base-line-height, $text-color);
    padding: 15px 0 15px 0;
    font-weight: 500;
}

h1 {
    @include font($font-size-h1, $line-height-h1, $title-color);
    font-family: $title-font-family;
    padding: 15px 0 15px 0;
    font-weight: 500;
    letter-spacing: -0.2px;
    text-shadow: 0px 1px 5px #005053;
}

h2 {
    @include font($font-size-h2, $line-height-h2, $title-color);
    font-family: $title-font-family;
    padding: 15px 0 15px 0;
    font-weight: 600;
}

h3 {
    @include font($font-size-h3, $line-height-h3, $title-color);
    font-family: $title-font-family;
    padding: 10px 0 10px 0;
    font-weight: 600;
}
h4 {
    @include font($font-size-h4, $line-height-h4, #585858);
    font-family: $title-font-family;
    padding: 0px 0 15px 0;
    font-weight: 600;
}

/* text position */

.text-left {
    text-align: left!important
}

.text-center {
    text-align: center!important
}

.text-right {
    text-align: right!important
}

.nowrap {
    white-space: nowrap!important;
}


/* loader */

.loaded .main-wrapper {
    visibility: hidden;
}

.icon-load {
    background: url(../img/loader.gif) no-repeat left top;
    width: 40px;
    height: 40px;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -20px;
    margin-left: -20px;
    display: none;
}

.loaded .icon-load {
    display: block;
}


/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/

.content {
    min-width: $min-width;
    text-align: left;
    width: 100%;
    order: 2;
    -webkit-order: 2;
    flex-grow: 1;
    -webkit-flex-grow: 1;
    background: #ffffff;
    background-size: cover;
}

.content-right {
    display: block;
}

.box-cont {
    display: block;
    width: 100%;
    &__title {
        background: #00b1b7;
        padding: 14px 20px 14px 20px;
        position: relative;
        z-index: 2;
        h1 {
            color: #ffffff;
            padding: 0 0 0 0;
        }
        h2 {
            color: #ffffff;
            padding: 0 0 0 0;
            text-shadow: 0px 1px 5px #005053;
        }
        h3 {
            color: #ffffff;
            padding: 0 0 0 0;
            text-shadow: 0px 1px 5px #005053;
        }
        @include shadow(0px 2px 5px 0px rgba(55, 56, 58, 0.35));
    }
    &__cont {
        background: #ffffff;
        overflow: hidden;
    }
}

.user-info {
    display: block;
    padding-top: 15px;
    &__item {
        border-bottom: solid 1px #def2f1;
        padding: 6px 37px 17px 37px;
    }
    &__item.last{
        border-bottom: none;
    }
    &__photo {
        width: 64px;
        height: 64px;
        border: solid 1px #e4e5e7;
        padding: 1px;
    }
    &__photo-img {
        display: block;
        width: 100%;
        height: 60px;
        background: url(../img/img_avatar.png) no-repeat center bottom;
        background-size: cover;
        overflow: hidden;
        img {
            width: 100%;
            display: block;
        }
    }
    &__right {
        position: relative;
    }
    &__label {
        @include font(11px, 15px, #3c3c3c);
        font-weight: 500;
        font-family: 'SFUIDisplay';
        letter-spacing: -0.1px;
        padding: 6px 0px 3px 0px;
    }
}

.btn {
    border: solid 1px #7f7f7f;
    height: 28px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    padding: 6px 13px 0 13px;
    @include font(12px, 15px, #3c3c3c);
    font-weight: 500;
    font-family: 'SFUIDisplay';
    background: #ffffff;
    display: inline-block;
    &_reload {
        position: absolute;
        left: 82px;
        top: -28px;
    }
    &:hover {
        background: #00b1b7;
        border-color: #00b1b7;
        color: #ffffff;
    }
}
input.btn {
    padding-top: 0px;
}

.box-field {
    display: block;
    position: relative;
    &__label {
        @include font(12px, 15px, #7f7f7f);
        padding: 0 0 16px 0;
        display: block;
        float: none;
        font-weight: 500;
        font-family: 'SFUIDisplay';
    }
    &__text {
        @include font(11px, 15px, #7f7f7f);
        padding: 8px 0 1px 0;
        display: block;
        font-weight: 500;
        font-family: 'SFUIDisplay';
    }
    &__input+&__input{
        margin-top: 18px;
    }
}

.form-control {
    display: block;
    width: 100%;
    background: #ffffff;
    border: solid 1px #7f7f7f;
    background: #ffffff;
    height: 28px;
    @include font(14px, 16px, #3c3c3c);
    padding: 4px 14px 6px 14px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    font-weight: 500;
    font-family: 'SFUIDisplay';
    &::-webkit-input-placeholder {color:#3c3c3c;}
    &::-moz-placeholder          {color:#3c3c3c;}
    &:-moz-placeholder           {color:#3c3c3c;}
    &:-ms-input-placeholder      {color:#3c3c3c;}
}

