.list-mark{
    display: block;
    text-align: center;
    padding: 36px 0 0 0;
    &__item{
        display: inline-block;
        vertical-align: top;
        margin: 0px 7px 0px 7px;
        width: 33px;
        position: relative;
        padding: 0 0 0 0;
        
    }
    &__cont{
        display: block;
        position: relative;
        padding-top: 54px;
        cursor: pointer;
        &:before{
            width: 33px;
            height: 30px;
            content: '';
            position: absolute;
            left: 0px;
            top: 0px;
            display: block;
        }
        input{
            position: absolute;
            left: 0px;
            top: 0px;
            opacity: 0;
        }
        input + .styled-radio{
            display: block;
            width: 10px;
            height: 10px;
            margin: 0 auto;
            border: 1px solid #747474;
            border-radius: 50%;            
            background: #ffffff;
            box-shadow: 0 0 0 0px rgba(0, 0, 0, 0);
            vertical-align: middle;
            cursor: pointer;
        } 
        input:checked + .styled-radio:before{
            display: block;
            content: '';
            width: 6px;
            height: 6px;
            margin: 1px 0 0 1px;
            background: #3c3c3c;
            border-radius: 50%;
        }
    }
    &__item.mark1 .list-mark__cont:before{
        background: url(../img/icon_mark_1.png) no-repeat center top;
        background-size: 100% auto;
    }
    &__item.mark2 .list-mark__cont:before{
        background: url(../img/icon_mark_2.png) no-repeat center top;
        background-size: 100% auto;
    }
    &__item.mark3 .list-mark__cont:before{
        background: url(../img/icon_mark_3.png) no-repeat center top;
        background-size: 100% auto;
    }
    &__item.mark4 .list-mark__cont:before{
        background: url(../img/icon_mark_4.png) no-repeat center top;
        background-size: 100% auto;
    }
    &__item.mark5 .list-mark__cont:before{
        background: url(../img/icon_mark_5.png) no-repeat center top;
        background-size: 100% auto;
    }
    &__item.mark6 .list-mark__cont:before{
        background: url(../img/icon_mark_6.png) no-repeat center top;
        background-size: 100% auto;
    }
    &__item.mark7 .list-mark__cont:before{
        background: url(../img/icon_mark_7.png) no-repeat center top;
        background-size: 100% auto;
    }
}
