    width: 73px;
    height: 28px;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;
    input{
        position: absolute;
        left: 0px;
        top: 0px;
        opacity: 0;
    }
    &__style{
        position: absolute;
        left: 0px;
        top: 0px;
        background: #acacac;
        width: 100%;
        height: 100%;
        border-radius: 4px;
        z-index: 1;
        &:before{
            display: block;
            content: '';
            left: 2px; top: 2px;
            width: 33px; height: 24px;
            border-radius: 3px;
            position: absolute;
            transition: left 0.2s;
            background: #ffffff;
        }
    }
    input:checked + .switcher__style{
        background: #14b866;
        &:before{
            left: 38px;
        }
