/* reset */
$min-width: 320px; 
$max-width: 1800px; 
$base-bg: #FFF; 
$base-font-size: 13px; 
$base-line-height: 18px; 
$base-font-family: 'SFUIDisplay', Arial, sans-serif; 
$custom-font-family: 'SFUIDisplay', Arial, sans-serif; 
$link-color: #100da9;
$default-transition:all 0.2s linear;

/* main variables */

//text, titles
$text-color: #000;
$title-color: #3c3c3c;
$title-font-family: $custom-font-family;

$font-size-h1: 16px;
$line-height-h1: 20px;
$font-size-h2: 16px;
$line-height-h2: 20px;
$font-size-h3: 18px;
$line-height-h3: 22px;
$font-size-h4: 14px;
$line-height-h4: 18px;