|                                     FOOTER                                   |
================================================================================
*/
footer{
	background:#222222; 	 
	text-align:left;
	width:100%;
	min-width: $min-width;
	order: 3;	
    -webkit-order: 3;
    padding: 20px 0px 20px 0px;
    z-index: 4;
    .wrapper{
        padding-left: 27px;
        padding-right: 27px;
    }
}

.box-social{
    display: block;
    text-align: center;
    &__item{
        display: inline-block;
        vertical-align: top;
        width: 25px;
        height: 25px;
        margin: 0 5px 0 6px;
        background: #919191;
        border-radius: 50%;
        text-align: center;
        color: #FFF;
        font-size: 12px;
        line-height: 14px;
        padding-top: 6px;
        &:hover{
            background: #6d737f;
        }
    }
}
.footer-text{
    display: block;
    padding: 0 18px 0 18px;
    border-top: solid 1px #707070;
    border-bottom: solid 1px #707070;
    margin: 14px 0 0 0;
    text-align: center;
    h4{
        @include font(11px,16px,#ffffff);
        padding-bottom: 5px;
        font-family: 'SFUIText';
    }
    p{
        @include font(11px,16px,#ffffff);
        padding: 10px 0 10px 0;
        font-family: 'SFUIText';
        letter-spacing: -0.2px;
    }
    
}
.footer-left{
    display: block;
    text-align: center;
    padding: 15px 0 0 0;
}
.copyright{
    @include font(11px,16px,#ffffff);
    font-family: 'SFUIText';
    padding: 0px 0 0 0;
    letter-spacing: -0.2px;
}






